export default {
	props: {
		keyStatu: {
			type: Boolean,
			value: false
		},
		arrObj: {
			type: Object,
			value: false
		}
	},
	data() {
		return {
			val: '',
			id:0
		};
	},
	methods: {
		clean() {
			this.val = '';
		},
		cancel() {
			this.val = '';
			this.$emit('keyBoard');
			uni.showTabBar();
		},
		changeNum(data) {
			let str = String(data);
			if (data == 'delete') {
				this.val = this.val.substring(0, this.val.length - 1);
			} else {
				if (this.val.indexOf('.') >= 0 && data == '.') {
					uni.showToast({
						title: '格式不正确',
						duration: 1000,
						icon: 'none'
					});
					return;
				}
				this.val += str;
			}
		},
		toParent() {
			if (this.val == '') {
				uni.showToast({
					title: '还没有输入',
					duration: 1000,
					icon: 'none'
				});
			} else if (this.val == 0) {
				uni.showToast({
					title: '购买数量不能为零',
					duration: 1000,
					icon: 'none'
				});
			}else if (this.arrObj.is_float == 1 && !Number.isInteger(Number(this.val))) {
				uni.showToast({
					title: '购买数量不能为小数',
					duration: 1000,
					icon: 'none'
				});
			} else {
				var obj = {
					arrObj: this.arrObj,
					val: this.val
				};
				this.$emit('keyBoard', this.keyStatu);
				this.$emit('toParent', obj);
				this.val = '';
			}
		}
	}
};
